<template>
    <!-- <flex-column> -->
    <div style="max-width: 500px;">
        <v-container v-if="loading" class="text-xs-center">
            <v-progress-circular color="rgba(0,0,0,0.5)" indeterminate />
        </v-container>
        <template v-else>
            
            <flex-column-header class="border-bottom">
                <page-header type="tag">
                    <template v-slot:left>
                        <h3>Remove Tags <span class="small text-muted">{{ids.length}} items</span></h3>
                    </template>
                </page-header>
            </flex-column-header>
            <!-- <flex-column-body> -->

                <empty-text v-if="!tags || !tags.length">
                No tags were found on these {{ids.length}} items
            </empty-text>


            <v-container v-else>
                <!-- <pre>{{ids.length}}</pre> -->
                <div v-for="group in tags">
                    <!-- <label>{{group.plural}} to remove</label> -->
                    <v-select v-model="selectedTags" chips :return-object="true" item-text="title" item-value="_id" :items="group.entries" attach :label="`Select ${group.plural}`" multiple>
                    </v-select>
                    <!-- AUTOCOMPLETE -->
                    <!-- <v-autocomplete v-model="selectedTags" :return-object="true" item-text="title" item-value="_id" :items="group.entries" attach chips :label="`Select ${group.plural}`" multiple> -->
                    <!-- </v-autocomplete> -->
                </div>
            </v-container>
            <!-- </flex-column-body> -->
            <flex-column-footer class="border-top">
                <v-container px-0 py-2>
                    <v-layout>
                        <v-btn flat @click="dismiss()">
                            Cancel
                        </v-btn>
                        <v-spacer />
                        <v-btn color="primary" v-if="selectedTags.length" @click="act()">
                            Remove {{selectedTags.length}} {{selectedTags.length == 1 ? 'tag' : 'tags'}}
                        </v-btn>
                    </v-layout>
                </v-container>
            </flex-column-footer>
        </template>
    </div>
    <!-- </flex-column> -->
</template>
<script>
import ModalMixin from '@/mixins/ModalMixin';

export default {
    props: {
        options: {
            type: Object
        }
    },
    mixins: [ModalMixin],
    data() {
        return {
            loading: true,
            selectedTags: [],
        }
    },
    computed: {
        ids() {
            return this.$fluro.utils.arrayIDs(this.options.items);
        },
    },
    asyncComputed: {
        tags: {
            default: [],
            get() {

                var self = this;
                self.loading = true;


                return new Promise(function(resolve, reject) {

                    self.$fluro.api.post(`/tags/list`, {
                            ids: self.ids,
                        })
                        .then(function(res) {

                            var mapped = self.mapDefinitionItems(res.data, 'tag');
                            resolve(mapped);
                            self.loading = false;
                        })
                        .catch(function(err) {
                            reject(err);
                            self.loading = false;
                        });
                });
            }
        }
    },
    methods: {
        act() {
            var self = this;

            //Run a batch
            var promise = self.$batch.run({
                operation: 'remove tag',
                ids: self.ids,
                tags: self.$fluro.utils.arrayIDs(self.selectedTags),
            });

            //Close the modal with the promise
            return self.close(promise)


        },
        mapDefinitionItems(array, backup) {

            var self = this;

            if (!array || !array.length) {
                return;
            }

            return _.chain(array)
                .orderBy(function(tag) {
                    return String(tag.title).toLowerCase()
                })
                .reduce(function(set, entry) {

                    var key = entry.definition || backup;
                    var existing = set[key];
                    if (!existing) {
                        existing = set[key] = {
                            title: self.$fluro.types.readable(key, false, backup),
                            plural: self.$fluro.types.readable(key, true, backup),
                            key,
                            entries: [],
                        }
                    }
                    existing.entries.push(entry);
                    return set;
                }, {})
                .values()
                .orderBy(function(type) {
                    return type.key == backup
                })
                .value();
        },
    }
}
</script>
