<template>
    <div class="page-header">
        <template v-if="$vuetify.breakpoint.smAndUp">
            <v-container fluid px-0 py-2>
                <v-layout row wrap>
                    <v-flex d-flex align-center>

                        <div class="main-icon" v-if="showIcon">
                            <fluro-icon library="fas" :icon="icon" :type="basicType" />
                            
                        </div>
                        <slot name="left"></slot>
                    </v-flex>
                    <v-spacer v-if="$vuetify.breakpoint.smAndUp" />
                    <v-flex d-flex shrink align-center>
                        <slot name="right"></slot>
                    </v-flex>
                </v-layout>
            </v-container>
        </template>
        <template v-else>
            <v-container py-2 px-0 fluid>
                <v-layout row wrap>
                    <v-flex d-flex align-center>
                        <slot name="left"></slot>
                    </v-flex>
                    <v-flex d-flex shrink align-center>
                        <slot name="rightmobile"></slot>
                        <!-- <v-btn class="ma-0" icon @click="expanded = !expanded">
                            <fluro-icon icon="ellipsis-v" />
                        </v-btn> -->
                    </v-flex>
                </v-layout>
            </v-container>
            <!-- v-if="expanded" -->
            <!-- <v-container py-2 class="border-top" fluid> -->
            <!-- <slot name="right"></slot> -->
            <!-- </v-container> -->
        </template>
    </div>
</template>
<script>
export default {
    props: {
        type: {
            type: String,
            default:'',
        },
        icon: {
            type: String,
            default:'',
        }
    },
    computed: {
        showIcon() {
            return this.icon || this.basicType;
        },
        basicType() {

            var term = this.types.terms[this.type];
            if (term && term.definitionName) {
                return term.parentType ? term.parentType : term.definitionName;
            } else {
                return this.type;
            }
        }
    },
    data() {
        return {
            expanded: false,
            types:this.$fluro.types,
        }
    },
    methods: {

    }
}
</script>
