<template>
    <flex-column class="tabset">
        <flex-column-header v-if="tabs.length > 1">
            <div class="tabset-menu">
                <a flat v-for="(tab, index) in tabs" :class="{active:index == activeTabIndex}" @click="selectTab(index)">
                    {{tab.heading}}
                </a>
            </div>
        </flex-column-header>
        <flex-column class="tabset-content">
            <slot />
        </flex-column>
    </flex-column>
</template>
<script>
export default {
    props: {
        options: {
            type: Object
        }
    },
    provide() {
        return {
            tabs: this.tabs,
            activeTabIndex: this.activeTabIndex,
        }
    },
    data() {
        return {
            tabs: [],
            activeTabIndex: 0,
        }
    },
    methods: {
        selectTab(index) {
            var self = this;

            self.activeTabIndex = index;

            _.each(self.tabs, function(tab) {
                tab.active = tab.index == self.activeTabIndex;
            })
        }
    },
