<template>
    <!-- <flex-column> -->
    <div style="max-width: 500px;">
        <v-container v-if="loading" class="text-xs-center">
            <v-progress-circular indeterminate />
        </v-container>
        <template v-else>
            <flex-column-header class="border-bottom">
                <page-header type="tag">
                    <template v-slot:left>
                        <h3>Add Tags <span class="small text-muted">{{ids.length}} items</span></h3>
                    </template>
                </page-header>
            </flex-column-header>
            <!-- <flex-column-body> -->
            <v-container>
                <!-- <pre>{{ids.length}}</pre> -->
                <div v-for="definition in tagDefinitions">
                    <template v-if="$vuetify.breakpoint.xsOnly">
                        <v-select v-model="selectedTags" chips :return-object="true" item-text="title" item-value="_id" :items="definition.tags" attach :label="`Select ${definition.plural}`" multiple />
                    </template>
                    <template v-else>
                        <v-autocomplete v-model="selectedTags" chips :return-object="true" item-text="title" item-value="_id" :items="definition.tags" attach :label="`Select ${definition.plural}`" multiple />
                    </template>
                    <!-- AUTOCOMPLETE -->
                    <!-- <v-autocomplete v-model="selectedTags" :return-object="true" item-text="title" item-value="_id" :items="group.entries" attach chips :label="`Select ${group.plural}`" multiple> -->
                    <!-- </v-autocomplete> -->
                </div>
            </v-container>
            <!-- </flex-column-body> -->
            <flex-column-footer class="border-top">
                <v-container px-0 py-2>
                    <v-layout>
                        <v-btn flat @click="dismiss()">
                            Cancel
                        </v-btn>
                        <v-spacer />
                        <v-btn color="primary" v-if="selectedTags.length" @click="act()">
                            Add {{selectedTags.length}} {{selectedTags.length == 1 ? 'tag' : 'tags'}}
                        </v-btn>
                    </v-layout>
                </v-container>
            </flex-column-footer>
        </template>
    </div>
    <!-- </flex-column> -->
</template>
<script>
import ModalMixin from '@/mixins/ModalMixin';

export default {
    props: {
        options: {
            type: Object
        }
    },
    mixins: [ModalMixin],
    data() {
        return {
            loading: true,
            selectedTags: [],
        }
    },
    computed: {
        ids() {
            return this.$fluro.utils.arrayIDs(this.options.items);
        },
    },
    asyncComputed: {
        tagDefinitions: {
            default: [],
            get() {

                var self = this;
                self.loading = true;

                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get(`/tags/tree`)
                        .then(function(res) {
                            resolve(res.data);
                            self.loading = false;
                        })
                        .catch(function(err) {
                            reject(err);
                            self.loading = false;
                        });
                });
            }
        }
    },
    methods: {
        act() {
            var self = this;

            //Run a batch
            var promise = self.$batch.run({
                operation: 'add tag',
                ids: self.ids,
                tags: self.$fluro.utils.arrayIDs(self.selectedTags),
            });

            //Close the modal with the promise
            return self.close(promise)


        },

    }
}
</script>
