import Vue from 'vue';

import UserMixin from '@/mixins/UserMixin.js';

export default {


    mixins: [UserMixin],
    data() {

        var pinStore = this.$fluro.stats.getUserStore('pinned', true);

        return {
            pinning: false,
            pinned: false,
            pinStore: pinStore, //pinStore,
        }
    },
    created() {
        var self = this;
        self.pinStore.refresh().then(refreshComplete, refreshComplete);

        function refreshComplete() {
            self.pinning = self.pinStore.isStatting(self.itemID);
            self.pinned = self.pinStore.isStatted(self.itemID)
        }
    },
    computed: {
        itemID() {
            var itemID = this.item;

            if (itemID._id) {
                itemID = itemID._id;
            }

            return itemID;
        },
        pinIcon() {
            if (!this.itemID) {
                return ['far', 'thumbtack'];
            }

            if (this.pinning) {
                return ['far', 'spinner-third']
            }

            if (this.pinned) {
                return ['fas', 'thumbtack']
            } else {
                return ['far', 'thumbtack']
            }
        },
    },
    methods: {
        togglePin() {

            var self = this;
            self.pinning = true;
            self.pinStore.toggle(self.itemID).then(success, failed);

            function success(res) {

                self.pinned = self.pinStore.isStatted(self.itemID);
                self.pinning = false;

                if (res.data.total) {
                    //And enable notifications with a short message
                    self.$fluro.notify('Pinned')
                } else {
                    self.$fluro.notify('Unpinned')
                }


            }

            function failed(err) {
                self.pinned = self.pinStore.isStatted(self.itemID);
                self.pinning = false;

                self.$fluro.error(err);

            }
        },
    }
}