
///////////////////////////////

var service = {
    items:[],
    active:false,
    context:{},
}

///////////////////////////////

service.open = function(items, options) {

    
    if (!options) {
        options = {}
    }

    //Set the context
    service.context = options;

    service.items = items || [];

    if (service.items && service.items.length) {
        service.active = true;
    } else {
        service.active = false;
        service.items = [];
    }

   console.log('open actions')
}

///////////////////////////////

service.close = function() {
    service.active = false;
    service.context = {};
    service.items = [];
    // console.log('close actions');
}

///////////////////////////////

service.summary = function() {
    var cutoff = 3;
    var count = service.items.length;

    var first = _.map(service.items.slice(0, cutoff), function(item) {
        return item.preferredName || item.firstName || item.title;
    }).join(', ');
    
    if (count <= cutoff) {
        return first;
    } else {
        return `${first} and ${count -cutoff} more selected`
    }
}

///////////////////////////////

export default service;