<template>
    <v-slide-y-reverse-transition>
        <div class="action-footer" v-if="count && enabled && !$actions.active" @click.stop.prevent="showActions()">
            <v-container pa-0 fluid>
                <v-layout align-center>
                    <div>
                        <v-btn small content="Deselect" v-tippy icon flat @click.stop.prevent="$selection.deselectAll()">
                            <!-- Deselect {{count}} -->
                            <fluro-icon library="far" icon="times" />
                        </v-btn>
                    </div>
                    <v-spacer />
                    <v-flex  fill-height class="text-xs-center">
                        <!-- {{count}} items selected -->
                        <strong>{{count}} items selected</strong>
                        <!-- {{$selection.summary()}} -->
                    </v-flex>
                    <v-spacer />
                    <div>

                        <v-btn small content="Actions" v-tippy icon flat>
                            <!-- Deselect {{count}} -->
                            <fluro-icon library="far" icon="angle-right" />
                        </v-btn>

                         <!-- <v-btn small color="black"> -->
                            <!-- Actions -->
                            <!-- <span style="color: #fff;">Next</span> -->
                            <!-- <fluro-icon style="color: #fff;" right library="far" icon="arrow-right" /> -->
                        <!-- </v-btn> -->
                    </div>
                </v-layout>
            </v-container>
        </div>
    </v-slide-y-reverse-transition>
</template>
<script>
export default {
    props:{
        persist:{
            type:Boolean,
        }
    },
    data() {
        return {
            selection: this.$selection,
        }
    },
    methods: {
        showActions() {
            this.$actions.open(this.$selection.selection, {fromSelection:true});
        }
    },
    computed: {
        enabled() {
            return this.persist || this.mobile;
        },
        mobile() {
            return this.$vuetify.breakpoint.smAndDown;
        },
        count() {
            return this.selection.selection.length;
        },
    },
}
</script>
<style scoped lang="scss">
.action-footer {
    background: $highlight-color;
    color: #000;
}
</style>