<template>
    <v-app id="fluro">

        <transition name="modal">
            <modal :modal="modal" v-for="modal in modals" />
        </transition>
        <!-- @next="nextPhoto" @previous="previousPhoto" -->
        <photo v-if="$route.query.photo" :id="$route.query.photo"></photo>
        <template v-if="!sidebarDisabled">
            <main-sidebar></main-sidebar>
        </template>
        <div class="main-content" :class="mutedClass" @click="hideDrawers()">
            <fluro-page-preloader v-if="$batch.loading" />
            <flex-column>
                <flex-column-header v-if="!headerDisabled">
                    <main-header></main-header>
                    <!-- <div class="breadcrumb-bar" v-if="breadcrumb.length"> -->
                        <!-- <breadcrumb /> -->
                    <!-- </div> -->

                     <!-- {{routeCacheKey}} -->
                </flex-column-header>

                <keep-alive>
                    <router-view :key="routeCacheKey"></router-view>
                </keep-alive>
                <flex-column-footer>
                    <div class="footer">
                        <template v-for="item in $batch.allTasks">
                        <v-slide-y-reverse-transition>
                            <batch-item :item="item"  />
                        </v-slide-y-reverse-transition>
                        </template>
                        <action-footer></action-footer>
                    </div>
                </flex-column-footer>
            </flex-column>
        </div>
        <template v-if="!actionCartDisabled">
            <action-cart></action-cart>
        </template>
    </v-app>
</template>
<script>
//Get the state from our Vuex store
// import { mapGetters } from 'vuex'

//Get the UI components
// import MainMenu from '@/components/layout/MainMenu.vue';
import MainSidebar from '@/components/layout/MainSidebar.vue';
import Modal from '@/components/modal/Modal.vue';
// import MainToolbar from '@/components/layout/MainToolbar.vue';
import MainHeader from '@/components/layout/MainHeader.vue';

import ActionCart from '@/components/layout/ActionCart.vue';
import ActionFooter from '@/components/layout/ActionFooter.vue';


import Breadcrumb from '@/components/Breadcrumb.vue';
import BatchItem from '@/components/BatchItem.vue';



//Include our SEO Mixin
import SEOMixin from '@/mixins/SEOMixin';
import UserMixin from '@/mixins/UserMixin';
import UIMixin from '@/mixins/UIMixin';
import BreadcrumbMixin from '@/mixins/BreadcrumbMixin';
import { Layout } from 'fluro-vue-ui';


////////////////////////////////

import Photo from '@/components/photos/Photo.vue';

////////////////////////////////

export default {
    components: {
        // MainMenu,
        BatchItem,
        Modal,
        MainSidebar,
        MainHeader,
        // MainToolbar,
        ActionCart,
        ActionFooter,
        Breadcrumb,
        Photo,
    },
    mixins: [SEOMixin, BreadcrumbMixin, UIMixin, UserMixin, Layout],
    data() {
        return {
            cacheKey:Math.random(),
            modals: this.$fluro.global.modals,
        }
    },
    created() {
       
        this.$fluro.addEventListener('cache.reset', this.resetCacheKey);
    },
    beforeDestroy() {
        this.$fluro.removeEventListener('cache.reset', this.resetCacheKey);
    },
    methods: {
        resetCacheKey() {
            console.log('RESET CACHE KEY');
            this.cacheKey = Math.random();
        },
        hideDrawers() {

            // console.log('Hide drawers!')
            // // if(this.$selection.showActionsForDesktop) {
            // //      this.$selection.showActionsForDesktop = false
            // // } else {
            //      this.$selection.showActionsForDesktop = false
            // // }

            this.drawer = false;

            this.$actions.close();
            // this.$selection.showActionsForMobile = false
            // this.$selection.showActionsForDesktop = false
        },
    },
    computed: {
        routeCacheKey() {

            var accountID = this.user ? this.$fluro.utils.getStringID(this.user.account) : '';
            return `${this.cacheKey}-${accountID}-${this.$route.name}`;
        },
        mutedClass() {
            if (this.mobile && this.drawer) {
                return 'faded';
            }
        },
        bleed() {
            var self = this;
            return _.get(self, '$route.meta.bleed');
        },
        sidebarDisabled() {
            var self = this;
            return self.bleed || !self.user;
        },
        actionCartDisabled() {
            var self = this;
            return self.bleed || !self.user;
        },
        headerDisabled() {
            var self = this;
            return self.bleed || _.get(self, '$route.meta.disableHeader');
        },
        footerDisabled() {
            var self = this;
            return self.bleed || _.get(self, '$route.meta.disableFooter');
        },
        authenticated() {

            //Returns true if we are running as an app or logged in as a user
            var isUser = (this.user);
            var isApplication = (this.application && this.application.authenticationStyle == 'application');

            if (isUser || isApplication) {
                return true;
            }

            //We're not authenticated at all
            return false;
        }
    },
}
</script>
<style lang="scss">
@import '@/styles/styles.scss';


.breadcrumb-bar {
    padding-left: 10px;
    background: linear-gradient(rgba(#fff, 0), #fff);
}



.modal-enter-active,
.modal-leave-active {
    transition: opacity .3s;
}

.modal-enter,
.modal-leave-to

/* .fade-leave-active below version 2.1.8 */
    {
    opacity: 0;
}
</style>