var router = new Router({
    mode: 'history',
    routes: array,
    scrollBehavior(to, from, savedPosition) {

        //Keep track of where the user was scrolled
        //if they hit the back button
        var pos = 0;
        // Scroll to the top
        if (savedPosition) {
            pos = savedPosition.y;
        }
        document.body.scrollTop = document.documentElement.scrollTop = pos;
    },
});

///////////////////////////////////



router.beforeEach((to, from, next) => {

    // console.log('BEFORE!')

    //Close the drawer whenever we change route
    // store.commit('ui/drawer', false)
    // 
    if (Vue.prototype.$vuetify.breakpoint.smAndDown) {
        store.commit('ui/drawer', false)
    } else {

        store.commit('ui/drawer', false)
        // switch (Vue.prototype.$drawerStyle) {
        //     case 'collapse':
        //         store.commit('ui/drawer', true)
        //         store.commit('ui/drawerMini', true)
        //         break;
        //     default:
        //         //Hide the drawer
        //         store.commit('ui/drawer', false)
        //         break;
        // }
    }



    if (to.meta) {
        //Get the user session from fluro
        var user = store.getters['fluro/user'];

        //If the route doesn't allow logged in users
        if (to.meta.requireUser) {
            if (user) {
                return next();
            }

            console.log('Route is only accessible to logged in users', to.fullPath)
            RouterHistory.intendedPath = to.fullPath;

            return next('/user/login')
        }

        //If the route only allows logged in users
        if (to.meta.denyUser) {
            if (!user) {
                return next();
            }

            console.log('Route is not accessible to logged in users')
            return next('/')
        }
    }


    return next();
})

///////////////////////////////////

RouterHistory.init(router);


///////////////////////////////////

export default router;

