    },
    beforeDestroy() {
        this.stopSelectionSync();
    },
    methods: {
        syncWithSelection() {

            //Update with our now selection
            var openCartByDefault = !previousSelection; // previousSelection != this.selection.selection.length;
            previousSelection = this.selection.selection.length;

            //If the selection is changed and cleared
            if (!this.selection.selection.length) {
                //We close the actions toolbar
                return this.actions.close();
            }

            if (this.$vuetify.breakpoint.xsOnly) {
                return;
            }

            if (this.actions.active) {
                this.actions.open(this.selection.selection, { fromSelection: true });
            }


            if (this.selection.selection.length) {
                if (openCartByDefault) {
                    return this.actions.open(this.selection.selection, { fromSelection: true });
                }
            }


        },
        startSelectionSync() {
            this.selection.addEventListener('selection', this.syncWithSelection)
        },
        stopSelectionSync() {
            this.selection.removeEventListener('selection', this.syncWithSelection)
        },
        canBeMerged(items) {

            var self = this;

            if (!items || !items.length || items.length <= 1) {
                return;
            }

            ///////////////////////////////////

            return _.chain(items)
                .reduce(function(set, item) {

                    ///////////////////////////////////

                    var titleKey = String(`${item.title}`).toLowerCase();
                    if (!set[titleKey]) {
                        set[titleKey] = 0;
                    }
                    // console.log('PUSH', titleKey)
                    set[titleKey]++;

                    ///////////////////////////////////

                    if (item.preferredName) {
                        var preferredNameKey = String(`${item.preferredName} ${item.lastName}`).toLowerCase();

                        if (item.preferredName && preferredNameKey != titleKey) {
                            if (!set[preferredNameKey]) {
                                set[preferredNameKey] = 0;
                            }
                            // console.log('PUSH', preferredNameKey)
                            set[preferredNameKey]++;
                        }
                    }

                    ///////////////////////////////////

                    if (item.firstName) {

                        var firstNameKey = String(`${item.firstName} ${item.lastName}`).toLowerCase();

                        if (preferredNameKey != firstNameKey && titleKey != firstNameKey) {
                            if (!set[firstNameKey]) {
                                set[firstNameKey] = 0;
                            }
                            // console.log('PUSH', firstNameKey)
                            set[firstNameKey]++;
                        }
                    }

                    ///////////////////////////////////

                    return set;
                }, {})
                // .values()
                .some(function(i, k) {
                    //Check if any of the items all match
                    if (i == items.length) {
                        return true;
                    }
                })
                .value();

        },
    },
    watch: {
        'actions.active': function(val) {

            if (val) {
                // console.log('REFS', this.$refs.content.$el);
                this.$refs.content.$el.scrollTop = 0;
            }

        }
    },
    asyncComputed: {
        allAvailableDefinitions: {
            default: [],
            get() {
                var self = this;

                if (!self.singleBasicType) {
                    return Promise.resolve([]);
                }

                ////////////////////////////////////////////

                var firstType = self.singleBasicType;

                ////////////////////////////////////

                var valueStorageCache = self.$fluro.cache.get('type-definitions');
                var cachedValue = valueStorageCache[firstType];

                if (cachedValue) {
                    return Promise.resolve(cachedValue)
                }

                ////////////////////////////////////


                self.loadingDefinitions = true;
                return new Promise(function(resolve, reject) {
                    //Update all of the definitions
                    return self.$fluro.types.retrieve([
                            firstType
                        ])
                        .then(function(res) {

                            var returningValue = _.get(res, '[0].definitions') || [];

                            if (returningValue.length) {

                                var originalType = res[0];
                                if (originalType) {
                                    // console.log('ORIGINAL TYPE', originalType, res);
                                    returningValue.unshift({
                                        title: originalType.title,
                                        definitionName: '',
                                    })
                                }
                            }

                            cachedValue = valueStorageCache[firstType] = returningValue;
                            resolve(cachedValue);
                            self.loadingDefinitions = false;
                        })
                        .catch(function(err) {
                            cachedValue = valueStorageCache[firstType] = null;
                            reject(err);
                            self.loadingDefinitions = false;
                        });
                });
            }

        },
        allPostableTypes: {
            default: [],
            get() {
                var self = this;


                return new Promise(function(resolve, reject) {
                    //Update all of the definitions
                    return self.$fluro.types.retrieve([
                            'post'
                        ])
                        .then(function(res) {
                            return resolve(_.get(res, '[0].definitions'));
                        })
                        .catch(reject);

                });

                // .then(function(postables) {
                //     console.log('All Definitions', postables);
                // }, function(err) {
                //     console.log('ERROR', err);
                // });



                var self = this;

                ////////////////////////////////////

                if (!self.selectedTypes || !self.selectedTypes.length) {
                    self.loadingPosts = false;
                    return Promise.resolve();
                }

                ////////////////////////////////////

                var valueStorageCache = self.$fluro.cache.get('post-parent-types');
                var typesAsString = _.orderBy(self.selectedTypes).join('');
                var cachedValue = valueStorageCache[typesAsString];

                if (cachedValue) {
                    return Promise.resolve(cachedValue)
                }

                ////////////////////////////////////

                self.loadingPosts = true;
                return new Promise(function(resolve, reject) {

                    return self.$fluro.api.post('/post/types?list=true', {
                            types: self.selectedTypes,

                        })
                        .then(function(res) {
                            resolve(res.data);
                            cachedValue = valueStorageCache[typesAsString] = res.data;
                            self.loadingPosts = false;
                        })
                        .catch(function(err) {
                            cachedValue = valueStorageCache[typesAsString] = null;
                            reject(err);
                            self.loadingPosts = false;
                        });
                })
            }

        }
    },
    computed: {

        actionLibraryLookup() {

            var self = this;

            return _.reduce(self.actionLibrary, function(set, action) {

                if (action.relevance && action.relevance.length) {
                    _.each(action.relevance, function(type) {
                        if (!set[type]) {
                            set[type] = [];
                        }

                        set[type].push(action);
                    })
                } else {
                    if (!set['all']) {
                        set['all'] = [];
                    }

                    set['all'].push(action);
                }

                return set;
            }, {});
        },
        allAvailablePostTypes() {

            var self = this;

            return _.filter(self.allPostableTypes, function(postType) {
                var parentTypes = _.get(postType, 'data.postParentTypes');

                // console.log('POST TYPE', postType);
                return (!parentTypes || !parentTypes.length) || _.intersection(parentTypes, self.selectedTypes).length;
            })
        },
        allAvailableActions() {

            var self = this;

            var allTypes = [].concat(self.selectedTypes, ['all'])

            return _.chain(allTypes)
                .map(function(type) {
                    return self.actionLibraryLookup[type];
                })
                .flatten()
                .reduce(function(set, action) {

                    if (!action) {
                        return set;
                    }

                    if (action.hidden) {
                        if (action.hidden(self.count, self.item, self.items)) {
                            return set;
                        }
                    }

                    ////////////////////////////////////////////

                    var groupingKey = action.group;
                    var existing = set[groupingKey];
                    if (!existing) {
                        existing = set[groupingKey] = {
                            title: groupingKey,
                            actions: [],
                        }
                    }

                    existing.actions.push(action);

                    return set;
                }, {})
                .values()
                .orderBy(function(group) {
                    switch (String(group.title).toLowerCase()) {
                        case '':
                            return -10;
                            break;
                        case 'actions':
                            return -9;
                            break;
                        case 'change status':
                            return -8;
                            break;
                        case 'advanced':
                            return 5;
                            break;
                        default:
                            return 0; //group.title;
                            break;
                    }
                })
                .value();
        },
        availableActions() {

            var self = this;

            return actions;

            //////////////////////////////////////////

            if (self.item) {

                var options = [];

                //Contact Options
                switch (self.item._type) {
                    case 'contact':
                        options.push({
                            title: `View ${self.item.preferredName || self.item.firstName || self.item.title}'s profile`,
                            icon: 'user',
                            action() {
                                self.$fluro.global.view(self.item);
                                return self.selection.deselectAll();
                            }
                        })
                        break;
                    default:
                        options.push({
                            title: `View '${self.item.title}'`,
                            icon: 'arrow-right',
                            action() {
                                self.$fluro.global.view(self.item);
                                return self.selection.deselectAll();
                            }
                        })
                        break;
                }

                //Include all the other options for this _type of item
                options = options.concat(self.getActionsFor(self.item._type));

                //And if it has a definition, include the options for that aswell
                if (self.item.definition) {
                    options = options.concat(self.getActionsFor(self.item.definition));
                }

                //Return early
                return _.chain(options)
                    .filter(function(option) {
                        return !option.hidden
                    })
                    .uniqBy(function(option) {
                        return option.key;
                    })
                    .value();
            }

            //////////////////////////////////////////
            //////////////////////////////////////////
            //////////////////////////////////////////
            //////////////////////////////////////////

            console.time('available actions')
            //Get a map of all the actions for available types
            var actions = _.chain(self.selectedTypes)
                // .reduce(function(set, item) {
                //     if (item._type) {
                //         set[item._type] = true;
                //     }

                //     if (item.definition) {
                //         set[item.definition] = true;
                //     }

                //     return set;
                // }, {})
                // .keys()
                .map(function(key) {
                    return self.getActionsFor(key);
                })
                .flatten()
                .compact()
                .filter(function(option) {
                    return !option.hidden
                })
                .uniqBy(function(option) {
                    return option.key;
                })
                .value();


            console.timeEnd('available actions')

            return actions;


        },
        item() {
            if (this.count == 1) {
                return _.first(this.$actions.items);
            }
        },
        items() {
            return this.$actions.items;
        },
        selectedTypes() {
            var types = _.chain(this.items)
                .reduce(function(set, item) {

                    set[item._type] = true
                    if (item.definition && item.definition.length) {
                        set[item.definition] = true;
                    }

                    return set;
                }, {})
                .keys()
                .value();

            return types;
        },
        selectedBasicTypes() {
            var types = _.chain(this.items)
                .reduce(function(set, item) {
                    set[item._type] = true
                    return set;
                }, {})
                .keys()
                .value();
            return types;
        },
        singleBasicType() {

            var type;

            if (this.selectedBasicTypes.length == 1) {
                type = this.selectedBasicTypes[0];
            }
            return type;

        },
        fromSelection() {
            return this.$actions.context.fromSelection;
        },
        selectionSummary() {

            return this.$actions.summary();


        },
        count() {
            return this.$actions.items.length;
        },
        plural() {

            var self = this;
            var word = 'item';
            var wordPlural = 'items';

            if (!self.selectedBasicTypes) {
                // console.log('No selected types');
                return self.count == 1 ? word : wordPlural;
            }

            if (self.selectedBasicTypes.length > 1) {
                // console.log('More than one type');
                return wordPlural;
            }

            // console.log(self.selectedBasicTypes);
            return self.count == 1 ? self.$fluro.types.readable(self.selectedBasicTypes[0]) : self.$fluro.types.readable(self.selectedBasicTypes[0], true);

        },
        temporary() {
            return true; //false;//true;
        },
        mobile() {
            return this.$vuetify.breakpoint.smAndDown;
        },
        drawerOpen: {
            get() {

                return this.actions.active;

                // if (this.mobile) {
                //     console.log('Actionable!')
                //     return this.selection.showActionsForMobile;
                // }

                // return this.count && this.selection.showActionsForDesktop;
            },
            set(value) {

            }
        },
    },
}
