import Vue from 'vue';
import _ from 'lodash';




///////////////////////////////

var interval = 1000;
var timer;



export default new Vue({

    data() {
        return {
            tasks: {},
            polling: false,
            loading:false,
            inflight: false,
        }
    },
    computed: {
        allTasks() {
            var self = this;
            //Get all tasks that require processing
            return _.values(self.tasks);
        },
        queue() {
            var self = this;
            //Get all tasks that require processing
            return _.filter(self.allTasks, function(item) {
                switch (item.task.status) {
                    case 'complete':
                    case 'prompt':
                        return;
                        break;
                    default:
                        return true;
                        break;
                }
            })

        },
    },
    methods: {
        complete(item) {

            var self = this;

            //We need to update the status
            //and resolve or reject the promise
            // console.log('Batch > Task Complete')
            var task = item.task;
            self.$fluro.notify(`${task.title} completed`)
            
            console.log('CLEAR CACHE')
            self.$fluro.cache.reset();
            self.$fluro.dispatch('cache.reset')


            item.resolve(task)

            setTimeout(function() {
                self.removeFromQueue(item.taskID);
            }, 10000);

        },
        prompt(item) {

            var self = this;


            var self = this;

            //We need to update the status
            //and resolve or reject the promise
            // console.log('Batch > Task Complete')
            var task = item.task;
            self.$fluro.notify(`${task.title} completed`)
            item.resolve(task)

            // setTimeout(function() {
            //     self.removeFromQueue(item.taskID);
            // }, 10000);
        },

        acknowledge(taskID) {

            var self = this;
            

            var item = self.tasks[taskID];
            var task =item.task;

            ///////////////////////

            if (task.result && task.result.downloadPath && task.result.downloadPath.length) {
                
                var url = `${self.$fluro.apiURL}/${task.result.downloadPath}`;
                console.log('Opening', url)
                self.$fluro.notify('Your popup blocker may stop this file from downloading');

                var access_token = self.$fluro.auth.getCurrentToken();
                if (access_token) {
                    window.open(url + '?access_token=' + access_token);
                } else {
                    window.open(url);
                }

                

            } 


            //Remove the item from the queue
                self.removeFromQueue(taskID)


            



        },

        removeFromQueue(taskID) {
            var self = this;
            Vue.delete(self.tasks, taskID);
        },
        requestStatusFromServer(ids) {

            var self = this;

            if (self.inflight || !ids || !ids.length) {
                //there is already a request in progress
                return;
            }

            /////////////////////////////////

            self.inflight = true;

            /////////////////////////////////

            // console.log('Batch > Request Status')
            //Check to see the status of these tasks
            self.$fluro.api.post('/tasks', {
                    ids,
                })
                .then(function(res) {
                    // console.log('RESPONSE', res.data);
                    _.each(res.data, function(task) {
                        //Update the queued item with our new task


                        var queuedItem = self.tasks[task._id];
                        if (!queuedItem) {
                            return;
                        }

                        //Update the task with the new details
                        var previousState = queuedItem.task;

                        //Update with new status
                        queuedItem.task = task;

                        //The state has changed
                        if (previousState.status != task.status) {

                            //If it was previously processing
                            //but now it is not we need to resolve the outcome
                            switch (task.status) {
                                case 'prompt':
                                    self.prompt(queuedItem);
                                    break;
                                case 'complete':
                                    self.complete(queuedItem);
                                    break;
                            }
                        }
                    });

                    self.inflight = false;

                })
                .catch(function(err) {
                    console.log('Batch > Task Error', err)
                    this.inflight = false;
                });
        },

        abortTask(taskID) {
            var self = this;
            self.$fluro.api.delete(`/tasks/${taskID}`)
                .then(function(res) {

                    self.$fluro.notify(`Action cancelled`)
                    self.removeFromQueue(taskID);
                }, function(err) {
                    self.$fluro.error(err);
                })
        },


        run(details, callback) {
            var self = this;

            ///////////////////////////////////

            // self.loading = true;

            ///////////////////////////////////

            return new Promise(function(resolve, reject) {
                //  if(details.ids && details.ids.length < 10) {
                //     console.log('Set Immediate')
                //     details.immediate = true;
                // }

                ///////////////////////////////////

                console.log('Create background task');
                
                self.$fluro.api.post('/batch', details)
                    .then(function(res) {

                        // self.loading = false;
                        var task = res.data;
                        var taskID = task._id;

                        //Create an abort function
                        function abort() {
                            console.log('Abort')
                            self.abortTask(taskID)
                        }

                        function acknowledge() {
                            console.log('Acknowledge')
                            self.acknowledge(taskID);
                        }

                        //Push the task into the queue
                        self.$set(self.tasks, taskID, {
                            taskID,
                            task,
                            resolve,
                            reject,
                            abort,
                            acknowledge,
                        })

                        //Start listening for queue progress
                        self.startPolling();
                        if(callback) {
                            callback(task)
                        }
                    })
                    .catch(function(err) {

                        reject(err);
                        // self.loading = false;
                    })

            });
        },
        startPolling() {

            console.log('Start Polling')
            this.checkTaskProgress();

            //If we are already polling
            if (this.polling) {
                return;
            }


            /////////////////////////////////

            //Take note that we are polling 
            //to avoid double timers
            this.polling = true;
            timer = setInterval(this.checkTaskProgress, interval);
        },
        stopPolling() {
            clearInterval(timer);
            this.polling = false;
        },
        checkTaskProgress() {

            var self = this;
            //Get all tasks that require processing

            //////////////////////////////////////

            //If there are no items to process
            if (!self.queue.length) {
                //Stop polling
                return self.stopPolling();
            }

            /////////////////////////////////

            //Get all the IDs of the tasks that are in progress
            var ids = _.map(self.queue, 'taskID');
            self.requestStatusFromServer(ids);
        }
    }

})