<template>
    <div class="batch-item-outer">
        <div class="batch-item" :class="status">
            <v-progress-linear :value="progress"></v-progress-linear>
            <template v-if="status == 'processing'">
                <v-layout>
                    <v-flex align-center d-flex shrink class="pa-1">
                        <div class="spinner">
                            <v-progress-circular color="primary" :value="progress"></v-progress-circular>
                            <span>
                                <fluro-icon spin icon="spinner-third" />
                            </span>
                        </div>
                    </v-flex>
                    <v-flex align-center d-flex class="pa-1">
                        <div>
                            <div class="text-capitalize">{{progress}}% {{task.action}} ({{success.length}} of {{total}})</div>
                            <div class="sm text-muted"><em>{{seconds}}</em></div>
                        </div>
                    </v-flex>
                    <v-flex shrink class="text-xs-right">
                        <v-btn small @click="item.abort()">
                            Cancel
                        </v-btn>
                    </v-flex>
                </v-layout>
            </template>
            <template v-else>
                <v-layout>
                    <v-flex align-center d-flex shrink class="pa-1">
                        <div class="spinner">
                            <v-progress-circular color="primary" :value="progress"></v-progress-circular>
                            <span>
                                <fluro-icon icon="check" />
                            </span>
                        </div>
                    </v-flex>
                    <v-flex align-center d-flex class="pa-1">
                        <div>
                            <div class="text-capitalize">{{task.action}} Complete!</div>
                            <div class="sm text-muted"><em>{{success.length}} of {{total}} completed</em></div>
                        </div>
                    </v-flex>
                    <v-flex shrink class="text-xs-right">
                        <v-btn color="primary" small @click="item.acknowledge()">
                            Ok
                        </v-btn>
                    </v-flex>
                </v-layout>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        item: {
            type: Object,
            required: true,
        }
    },
    computed: {
        status() {
            return this.task.status;
        },
        task() {
            return this.item.task;
        },
        success() {
            return this.task.success || []
        },
        total() {
            return this.task.total || 0;
        },
        progress() {
            var self = this;
            return Math.floor((self.success.length / self.total) * 100)
        },
        seconds() {

            var self = this;
            var t1 = new Date(self.task.eta);
            var t2 = new Date();
            var dif = t1.getTime() - t2.getTime();
            var seconds = Math.round(dif / 1000);

            if (isNaN(seconds)) {
                return 'Calculating...';
            }

            if (seconds < 2) {
                return 'Wrapping up...';
            }

            return 'About ' + seconds + ' seconds remaining';
        }
    }
}
</script>
