import Vue from 'vue';
import _ from 'lodash';

///////////////////////////////

var service = {
    selection: [],
    showActionsForDesktop:false,
    showActionsForMobile: false,
    hashLookup: {},
    previousIntent: null,
}




/////////////////////////////////////////////

service.allSelected = function(group) {

    var selectedIDs = service.ids();
    var itemIDs = Vue.prototype.$fluro.utils.arrayIDs(group.items);

    console.log('All Selected', group.title, _.intersection(selectedIDs, itemIDs).length, group.items.length);
    return _.intersection(selectedIDs, itemIDs).length == group.items.length;
}


///////////////////////////////


service.select = function(item, options) {

    // console.log('SELECT', item)
    if (!options) {
        options = {}
    }

    ////console.log('Select', item)
    if (!service.isSelected(item)) {
        service.selection.splice(service.selection.length, 0, item);
        rehash()

        if (!options.suppressEvent) {
            service.dispatch('select', item);
            service.dispatch('selection');
        }


        service.previousIntent = 'select';
        // service.$set(service.selection, service.selection.length, item);
        // ////console.log('Select', service.selection.length, item);
    }
}


///////////////////////////////


service.setSelection = function(array) {
    service.selection = array || [];
    rehash()
    if (!service.selection.length) {
        service.showActionsForMobile = false;
        service.showActionsForDesktop = false;
    }


    service.dispatch('selection');

}



///////////////////////////////

service.deselect = function(item, options) {
    if (!item) {
        return;
    }

    //Get the item ID
    if (!options) {
        options = {}
    }

    ///////////////////////////////////////////////
    //Get the itemID
    var itemID = item._id || Vue.prototype.$fluro.utils.getStringID(item);

    //Find the index of the matching item
    var index = _.findIndex(service.selection, function(selectedItem) {
        var selectedItemID = Vue.prototype.$fluro.utils.getStringID(selectedItem);
        return itemID == selectedItemID;
    })

    if (index == -1) {
        return;
    }

    service.selection.splice(index, 1);

    if (!service.selection.length) {
        service.showActionsForMobile = false;
        service.showActionsForDesktop = false;
    }

    rehash()

    if (!options.suppressEvent) {
        service.dispatch('deselect', item);
        service.dispatch('selection');
    }

    service.previousIntent = 'deselect';

}

///////////////////////////////

service.toggle = function(item) {



    if (service.isSelected(item)) {
        service.deselect(item);
    } else {
        service.select(item);
    }

    // ////console.log('TOGGLE', service.selection);
}

///////////////////////////////

//Alias for deselectAll
service.clear =
service.deselectAll = function() {
    service.setSelection([]);
    // this.service.selection.length =0;

    if (!service.selection.length) {
        service.showActionsForMobile = false;
        service.showActionsForDesktop = false;
    }

    service.previousIntent = null;
}


///////////////////////////////

service.selectMultiple = function(items) {
    // console.time('select multiple')
    var newSelection = [...service.selection, ...items];
    newSelection = _.uniqBy(newSelection, function(item) {
        return item._id || item;
    });

    service.setSelection(newSelection)
    // console.timeEnd('select multiple')
    rehash()
    service.previousIntent = 'select';
    service.dispatch('selection');

}
///////////////////////////////

service.ids = function() {
    return Vue.prototype.$fluro.utils.arrayIDs(service.selection);
}

///////////////////////////////

service.hash = function() {
    return service.hashLookup;
    // _.reduce(service.selection, function(set, item) {
    //     var itemID = Vue.prototype.$fluro.utils.getStringID(item);
    //     set[itemID] = true;
    //     return set;
    // }, {});
}

///////////////////////////////

function rehash() {
    service.hashLookup = _.reduce(service.selection, function(set, item) {
        var itemID = Vue.prototype.$fluro.utils.getStringID(item);
        set[itemID] = true;
        return set;
    }, {});
}

///////////////////////////////


service.deselectMultiple = function(items) {

    var lookup = _.reduce(items, function(set, item) {
        var itemID = Vue.prototype.$fluro.utils.getStringID(item);
        set[itemID] = true;
        return set;
    }, {});


    _.each(service.selection, function(item, index) {
        var itemID = Vue.prototype.$fluro.utils.getStringID(item);
        if (lookup[itemID]) {
            service.selection[index] = null;
        }
    })

    //Remove the nulls
    service.previousIntent = 'deselect';
    service.setSelection(_.compact(service.selection));

}

///////////////////////////////

service.isSelected = function(item) {
    if (!item) {
        return;
    }

    var itemID = Vue.prototype.$fluro.utils.getStringID(item);

    return service.hashLookup[itemID];

    // //Check if the ID is already selected
    // var match = _.some(service.selection, function(selectedItem) {
    //     var selectedItemID = Vue.prototype.$fluro.utils.getStringID(selectedItem);
    //     return itemID == selectedItemID;
    // })

    // return match;
}


/////////////////////////////////////////////

service.summary = function() {
    var cutoff = 3;
    var count = service.selection.length;

    var first = _.map(service.selection.slice(0, cutoff), 'title').join(', ');
    if (count <= cutoff) {
        return first;
    } else {
        return `${first} and ${count -cutoff} more selected`
    }
}


/////////////////////////////////////////////
/////////////////////////////////////////////
/////////////////////////////////////////////
/////////////////////////////////////////////

var listeners = {};

/////////////////////////////////////////////

//Remove all listeners
service.removeAllListeners = function() {
    listeners = {};
}

/////////////////////////////////////////////

service.dispatch = function(event, details) {

    if (listeners[event]) {

        // //console.log('Listeners', event, listeners[event]);
        //For each listener
        listeners[event].forEach(function(callback) {
            //Fire the callback
            // ////console.log('Fire listener', event, details);
            return callback(details);
        });
    }
}

/////////////////////////////////////////////

service.addEventListener = function(event, callback) {

    if (!listeners[event]) {
        listeners[event] = [];
    }

    if (listeners[event].indexOf(callback) == -1) {
        //Add to the listeners
        listeners[event].push(callback)
    } else {
        //Already listening
    }
}

/////////////////////////////////////////////

service.removeEventListener = function(event, callback) {

    if (!listeners[event]) {
        listeners[event] = [];
    }

    //Get the index of the listener
    var index = listeners[event].indexOf(callback);

    if (index != -1) {
        //Remove from the listeners
        listeners[event].splice(index, 1);
    }
}




///////////////////////////////



export default service;